import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, PanelHeaderBack, View, Group, FormItem, SliderSwitch } from '@vkontakte/vkui';

import persik from '../img/persik.png';
import './Persik.css';

const Test = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Это Персик)
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
		




        <View activePanel="progress">
    <Panel id="progress">
      <PanelHeader>
        SliderSwitch
      </PanelHeader>
      <Group>
      <FormItem top="">
          <SliderSwitch 
            options={[
              {
                name: 'Мужской',
                value: 'male',
              },
              {
                name: 'Женский',
                value: 'female',
              },
            ]}
          />
        </FormItem>
      </Group>
    </Panel>
  </View>


	</Panel>
);




Test.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


export default Test;
